import type { SpacingLevels } from '../../foundations/designTokens';
import type { CSSProperties } from '../../utils/styled';
import { css, styled } from '../../utils/styled';
import { modifier, spacing } from '../../utils/theme';

type ContainerProps = {
	alignItems: CSSProperties['alignItems'];
	justifyContent: CSSProperties['justifyContent'];
	flexWrap: CSSProperties['flexWrap'];
	flexDirection: CSSProperties['flexDirection'];
	basis?: CSSProperties['flexBasis'];
	grow?: CSSProperties['flexGrow'];
	shrink?: CSSProperties['flexShrink'];
	alignSelf?: CSSProperties['alignSelf'];
	rowGap: SpacingLevels;
	columnGap: SpacingLevels;
	inline: boolean;
};

export const Container = styled.div<ContainerProps>`
	display: flex;
	gap: ${({ rowGap }) => spacing(rowGap)} ${({ columnGap }) => spacing(columnGap)};
	max-width: 100%;
	align-items: ${({ alignItems }) => alignItems};
	justify-content: ${({ justifyContent }) => justifyContent};
	flex-wrap: ${({ flexWrap }) => flexWrap};
	flex-direction: ${({ flexDirection }) => flexDirection};
	flex-basis: ${({ basis }) => basis};
	flex-grow: ${({ grow }) => grow};
	flex-shrink: ${({ shrink }) => shrink};
	align-self: ${({ alignSelf }) => alignSelf};
	${modifier(
		'inline',
		css`
			display: inline-flex;
		`,
	)}
`;
